import Vue from 'vue'
import VueRouter from 'vue-router'
import index from '../views/index.vue'

Vue.use(VueRouter)

const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
}

const routes = [
	{
		path: '/',
		name: 'index',
		component: index,
		children: [
			{
				path: '/group',
				name: 'group',
				component: () => import('../views/group.vue')
			},
			{
				path: '/list',
				name: 'list',
				component: () => import('../views/list.vue')
			},
			{
				path: '/user',
				name: 'user',
				component: () => import('../views/user.vue')
			},
			{
				path: '/team',
				name: 'team',
				component: () => import('../views/team.vue')
			},
			{
				path: '/404',
				name: '404',
				component: () => import('../views/404.vue')
			},
			{
				path: '/design',
				name: 'design',
				component: () => import('../views/design.vue')
			},
			{
				path: '/library',
				name: 'library',
				component: () => import('../views/library.vue')
			},
			{
				path: '/upload',
				name: 'upload',
				component: () => import('../views/upload.vue')
			},
			{
				path: '/choose_from_public',
				name: 'choose_from_public',
				component: () => import('../views/choose_from_public.vue')
			},
			{
				path: '/public_upload',
				name: 'public_upload',
				component: () => import('../views/public_upload.vue')
			},
			{
				path: '/gallery',
				name: 'gallery',
				component: () => import('../views/gallery.vue')
			},
			{
				path: '/designer_upload',
				name: 'designer_upload',
				component: () => import('../views/designer_upload.vue')
			},
			{
				path: '/public_gallery',
				name: 'public_gallery',
				component: () => import('../views/public_gallery.vue')
			}
		]
	},
	{
		path: '/project',
		name: 'project',
		component: () => import('../views/project.vue')
	},
	{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},
	{
		path: '/reg',
		name: 'reg',
		component: () => import('../views/reg.vue')
	},
	{
		path: '/share',
		name: 'share',
		component: () => import('../views/share.vue')
	},
	{
		path: '/u.library',
		name: 'u.library',
		component: () => import('../views/u.library.vue')
	},
	{
		path: '/u.edit.library',
		name: 'u.edit.library',
		component: () => import('../views/u.edit.library.vue')
	},
	{
		path: '/u.view.library',
		name: 'u.view.library',
		component: () => import('../views/u.view.library.vue')
	},
	{
		path: '/done',
		name: 'done',
		component: () => import('../views/done.vue')
	},
	{
		path: '/your_preference',
		name: 'your_preference',
		component: () => import('../views/your_preference.vue')
	},
	{
		path: '/tag_list',
		name: 'tag_list',
		component: () => import('../views/tag_list.vue')
	}
]

const router = new VueRouter({
	mode: 'hash',
	base: process.env.BASE_URL,
	routes
})

// 路由拦截，自定义404
let routesNameArray = [];
routes.map(item => {
	routesNameArray.push(item.name);
	item.children && item.children.map(i => {
		routesNameArray.push(i.name);
	})
})

router.beforeEach((to, from, next) => {
	// 判断是否在登陆状态
	const excludedPages = [
		'/u.library',
		'/u.edit.library',
		'/done',
		'/your_preference',
		'/share'
	];

	if (excludedPages.includes(to.path)) {
		next(); // Proceed to the page without redirection
	} else if (to.path !== '/login' && to.path !== '/reg' && to.query.custom !== '1' && !localStorage.getItem('designer_user')) {
		// Perform the login check for other pages
		next({ path: '/login' }); // Redirect to the login page
	} else if (routesNameArray.indexOf(to.name) === -1) {
		next({ path: '/404' }); // Redirect to the 404 page
	} else {
		next(); // Proceed to the page without redirection
	}
});

export default router
