<script>
	import global from '@/tools/global';
	export default {
		name: 'index',
		data (){
			return {
				lang: {},
				user: {},
				hi: '',
				menu: [],
				currentMenu: ''
			}
		},
		created (){
			// 多语言支持
			this.lang = this.$lang('index');
			this.menu = [ 
				{
					name: this.lang.menu.list,
					route: 'group',
					icon: 'el-icon-pie-chart'
				},
				{
					name: this.lang.menu.user,
					route: 'user',
					icon: 'el-icon-user'
				}
			];
			this.currentMenu = this.lang.menu.default;
		},
		mounted (){
			// 获取用户信息
			this.user = JSON.parse(localStorage.getItem('designer_user'));
			this.user.avatar = global.host + this.user.avatar;
			if(this.user.id == 0){
				this.menu = [];
			}
			if(this.user.role == 0 && this.user.id != 0){
				this.menu.push({
					name: this.lang.menu.team,
					route: 'team',
					icon: 'designer- designer-qunchengyuan-02'
				})
			}

			if(this.user.role == 0 && this.user.id != 0){
				this.menu.push({
					name: this.lang.menu.public_gallery,
					route: 'public_gallery',
					icon: 'designer- designer-qunchengyuan-02'
				})
			}

				this.menu.push({
					name: this.lang.menu.gallery,
					route: 'gallery',
					icon: 'designer- designer-qunchengyuan-02'
				})
			

			// 欢迎语
			if(this.user.role == 0){
				this.hi = `${this.lang.role.admin} ${this.user.user_name} ${this.lang.role.hi}`;
			}
			if(this.user.role == 1){
				this.hi = `${this.lang.role.designer} ${this.user.user_name} ${this.lang.role.hi}`;
			}
			if(this.user.role == 2){
				this.hi = `${this.lang.role.custom} ${this.user.user_name} ${this.lang.role.hi}`;
			}
			if(this.user.id == 0){
				this.hi = `Custom`;
			}

			// 默认进入 group 路由
			if(this.$route.name == 'index'){
				this.$router.push('/group');
			}

			// 刷新时菜单高亮
			switch (this.$route.name){
				case 'group':
					this.currentMenu = this.lang.menu.list;
					break;
				case 'list':
					this.currentMenu = this.lang.menu.list;
					break;
				case 'user':
					this.currentMenu = this.lang.menu.user;
					break;
				case 'team':
					this.currentMenu = this.lang.menu.team;
					break;
			}
		},
		methods: {
			// 切换菜单
			menuClick (item){
				this.currentMenu = item.name;
				this.$router.push(item.route);
			},
			// 切换语言
			switchLang (index){
				if(index == 0){
					localStorage.setItem('lang', 'zht');
					this.$message.warning('語言已切換，請重新登入');
				}
				if(index == 1){
					localStorage.setItem('lang', 'zh');
					this.$message.warning('语言已切换，请重新登录');
				}
				if(index == 2){
					localStorage.setItem('lang', 'en');
					this.$message.warning('Language has been switched, please login again');
				}
				localStorage.removeItem('designer_user');
				this.$router.push('/login');
			},
			// 退出登录
			logout (){
				let that = this;
				this.$confirm(this.lang.tips.logout.title, this.lang.tips.logout.t, {
                    type: 'warning',
					cancelButtonText: this.lang.tips.logout.cancel,
					confirmButtonText: this.lang.tips.logout.confirm,
                    beforeClose (action, instance, done){
                        if(action == 'confirm'){
                            that.$router.push('/login');
							localStorage.removeItem('designer_user');
                            done();
                        }else{
                            done();
                        }
                    }
                }).catch(() => {});
			}
		}
	}
</script>

<style lang="less" scoped>
	@import url('../tools/common.less');
    .index{
		width: 100%;
		height: 100%;

		.header {
			width: calc(100% - 30px);
            height: 105px;
            background: #FFFFFF;
            position: absolute;
            top: 0;
            left: 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
            color: #000000;
            font-weight: 500;
            padding: 0 15px;
			.logo {
				width:95px;
				margin:39px 41px;
			}
			.menu {
				font-size: 12px;
				margin-right: 25px;
				img {
					height: 40px;
					vertical-align: middle;
				}
				span{
					margin-right: 30px;
					cursor: pointer;
				}
			}
		}
        .main{
            width: 100%;
            height: calc(100% - 105px);
            position: absolute;
            top: 105px;
            left: 0;
			background-color: #FFFFFF;
            .view{
                width: calc(100% - 280px);
                height: calc(100% - 50px);
                position: absolute;
                top: 0;
                left: 240px;
				background-color: #F4F5F7;
				border-radius: 12px;
			}
            .sidebar{
                width: 160px;
                //height: calc(100% - 224px);
				height: 100%;
                background: #FFFFFF;
                //position: absolute;
                top: 0;
                left: 0;
				padding: 0px 40px;
				font-size: 12px;
				.sidebar_item {
					color: #000000;
					font-weight: 600;
					padding: 16px 14px;
					cursor: pointer;
					&.active {
						background: #F4F5F7;
						border-radius: 12px;
					}
				}
				.sidebar_signout {
					color: #000000;
					font-weight: 600;
					padding: 16px 14px;
					border: 1px solid #000000;
					border-radius: 12px;
					text-align: center;
					position: absolute;
					bottom: 50px;
					width: 130px;
					cursor: pointer;
				}
			}
		}



		/* 头部 */
		.head{
			width: calc(100% - 60px);
			height: 60px;
			border-bottom: 1px solid rgb(236, 236, 236);
			padding: 0 30px;
			background: #ffffff;
			position: absolute;
			top: 0;
			left: 0;
			z-index: 3;
			display: flex;
			justify-content: space-between;
			align-items: center;
			.user{
				display: flex;
				justify-content: flex-start;
				align-items: center;
				img{
					width: 45px;
					height: 45px;
					border-radius: 50%;
				}
				p{
					margin-left: 20px;
				}
				span{
					margin-left: 30px;
					cursor: pointer;
				}
			}
			.menu{
				display: flex;
				justify-content: flex-end;
				align-items: center;
				span{
					display: block;
					padding: 0 15px;
					height: 58px;
					line-height: 60px;
					margin: 0 10px;
					cursor: pointer;
					user-select: none;
					border-bottom: 2px solid transparent;
				}
				span.current{
					color: @color;
					border-bottom: 2px solid rgb(126, 172, 250);
				}
			}
		}


		/* 内容区 */
		.content{
			width: 100%;
			height: calc(100% - 61px);
			position: absolute;
			top: 60px;
			left: 0;
			z-index: 1;
		}
	}
</style>
